const api = require('./api')

const hexPrototype = (hex) => {
  let value = (hex.length === 3 || hex.length === 6) ? hex : undefined

  if (typeof value === 'undefined')
    throw Error('ERROR')

  return {
    ...api,
    format: 'hex',
    set(hexVal) {
      value = hexVal
      return this
    },
    get() {
      return value;
    },
    toString() {
      return `#${this.get()}`
    }
  }
}

module.exports = hexPrototype